import Upgrade from './upgrade'
import { UpgradeNames } from '../constants'

export default class CreditsPerPossibilityUpgrade extends Upgrade {
  getName (): string {
    return UpgradeNames.CREDITS_PER_POSSIBILITY
  }

  buy () {
    super.buy()
    this.credits.perPossibility += 1
  }

  get current () {
    return this.credits.perPossibility
  }

  get next () {
    return this.credits.perPossibility + 1
  }
}
