import Solver from './solver'
import { SolverNames } from '../constants'

export default class RowSolver extends Solver {
  getName () {
    return SolverNames.ROW_SOLVER
  }

  runSolver () {
    const row = this.puzzle[this.solvingSpot]
    return this.solveVals(row)
  }

  isSolving (x: number, y: number): boolean {
    return y === this.solvingSpot
  }

  isComplete (solvingSpot: number): boolean {
    return this.puzzle[solvingSpot].every((spot) => spot.length === 1)
  }

  shouldSolve (solvingSpot: number): boolean {
    const noSolves = this.noAutos.map((a) => a - 9)
    return !noSolves.includes(solvingSpot)
  }
}
