import Upgrade from './upgrade'
import { UpgradeNames } from '../constants'

export default class CreditsPerCellUpgrade extends Upgrade {
  getName (): string {
    return UpgradeNames.CREDITS_PER_CELL
  }

  buy () {
    super.buy()
    this.credits.perCell += 5
  }

  get current () {
    return this.credits.perCell
  }

  get next () {
    return this.credits.perCell + 5
  }
}
